.submit-log-form {
  position: absolute;
  left: 50%;
  top: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  border: 2px solid #216fb3;
  border-radius: 10px;
  padding: 10px;
  width: 80%;
}

.submit-button {
  color: blue;
}
